
*{
  box-sizing: border-box;
  padding:0;
  margin: 0;
}

.app{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}
.container{
  box-shadow: 0px 0px 12px #333;
  border-radius: 8px;
  padding: 1rem;
  border: 1px solid red;
  
}
input{
  width: 100%;
  font-size: 1.2rem;
  padding: 8px 4px;
  margin: 8px 0;
  border-radius: 8px;
}
.btn{
  width: 100%;
  display: block;
  font-size: 1.2rem;
  margin: 8px 0;
  padding: 10px 0;
  background-color: #333;
  color: white;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;


}
.btn:hover{
  font-size: 1.3rem;
   
}
.btn-outline{
  background-color: rgb(57, 164, 214);
  color: #333;
}
.center{
  text-align: center;
  margin: 24px 0;
}
p{
  margin: 10px 0;
}
.img-container{
  text-align: center;
}
.img-container img{
  height: 200px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
